import React, { useState, useEffect } from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css"
import MovieList from './components/MovieList';
import MovieListHeading from './components/MovieListHeading';
import SearchBox from './components/SearchBox';
import AddFavourite from './components/AddFavourites';
import RemoveFavourite from './components/RemoveFavourite';

const App = () => {
  const [movies, setMovies] = useState([]);
  const [favourites, setFavourites] = useState([]);
  const [searchValue, setSearchValue] = useState("");

  const getMovieRequest = async (searchValue) => {
    const url = `http://www.omdbapi.com/?s=${searchValue}&apikey=d993ce47`

    const response = await fetch(url);
    const responseJson = await response.json();

    if (responseJson.Search) {
      
      setMovies(responseJson.Search)
    }
  };

  useEffect(() => {
    getMovieRequest(searchValue);
  }, [searchValue]);

  useEffect(()=>{
    const movieFavourites = JSON.parse(localStorage.getItem("react-movie-app-favourites"));
    setFavourites(movieFavourites);
  }, [])

  const saveToLocalStorage = (items) =>{
    localStorage.setItem("react-movie-app-favourites", JSON.stringify(items))
  }

  const addFavouriteMovie = (movie) =>{
    const newFavoureiteList = [...favourites, movie];
    setFavourites(newFavoureiteList);
    saveToLocalStorage(newFavoureiteList);
  };

  const removeFavouriteMovie = (movie) =>{
    const newFavoureiteList = favourites.filter((favourite)=>favourite.imdbID !== movie.imdbID);

    setFavourites(newFavoureiteList);
    saveToLocalStorage(newFavoureiteList);
  }

  return (
    <div className="container-fluid movie-app">
      <div className="row d-flex align-items-center mt-4 mb-4">
        <MovieListHeading heading="Movies" />
        <SearchBox searchValue={searchValue} setSearchValue={setSearchValue} />
      </div>
      <div className="col d-flex">
      <MovieList movies={movies} handleFavouritesClick={addFavouriteMovie} favouriteComponent={AddFavourite} />
     
      </div>
      <div className="row d-flex align-items-center mt-4 mb-4">
        <MovieListHeading heading="Favourites" />
      </div>
      <div className="col d-flex">
      <MovieList movies={favourites} handleFavouritesClick={removeFavouriteMovie} favouriteComponent={RemoveFavourite} />
     
      </div>
    </div>
  )
}

export default App
